import react, { useContext } from 'react';
import { getCSS } from './props';
import { EditArea, AlingContext } from '../../SlateCustom'
import { Row, Col } from 'react-bootstrap';
import { State } from './index'

export function Panel({ config: { ori, text, textColor, backgroundColor, aspect, xs, sm, md, lg, xl, xxl, size, video } }: { config: State }) {
    const { HAlign } = useContext(AlingContext);
    const invert = (orie: boolean) => {
        if (!text || orie)
            return <div style={{ width: l + '%', position: 'relative', float: 'left' }}>
                <iframe src={video} style={{ width: '100%', aspectRatio: aspect }}></iframe>
            </div>
        return <div style={{ width: ((l === 100) ? 100 : 100 - l) + '%', backgroundColor: textColor, position: 'relative', float: 'left', }}>
            <div style={{
                padding: '30px',
                margin: 0,
                position: 'relative',
                height: '100%',
                float: 'left'
            }} className={`text-${HAlign}`}>
                <EditArea />
            </div>
        </div>
    }
    const t = !text ? 100 :
        (size === '570px') ? xs :
            (size === '780px') ? sm :
                (size === '990px') ? md :
                    (size === '1080px') ? lg :
                        (size === '1320px') ? xl : xxl;
    const l = (t > 70) ? 100 : t;
    return <div style={{ padding: '60px 0', backgroundColor }}>
        <div className="container">
            <Row className='d-flex'>
                {invert(ori)}
                {text ? invert(!ori) : <></>}
            </Row>
        </div>
    </div>
}