import react from "react"

export function Svg({ children }: any) {
    return <svg style={{
        fillRule: 'evenodd',
        clipRule: 'evenodd',
        strokeLinejoin: 'round',
        strokeMiterlimit: 2,
        border: '1px solid black',
        backgroundColor: '#fff'
    }} width="72" height="48" viewBox="0 0 400 253" version="1.1" xmlns="http://www.w3.org/2000/svg" >
        {children}
    </svg>
}
