import react, { useState, useContext, useEffect } from 'react';
import { Card, Row, Button, FormControl, InputGroup, ButtonGroup } from 'react-bootstrap';
import { InputNumber, InputText } from '../../../../Utiles/Inputs';
import { SetSlider, SizeViewSelector, Title, Guardar, ButtonColor, TitleCard, CardConfig, RowConfig, AddItens } from '../../Crear/component';
import { State, iten, itenUpdate } from './index'
import { ConfigIten } from './Itens/Config';
import { Selector } from './Selector';
import { TBElementLite } from '../../SlateCustom';
interface Props {
    iten: iten;
    title: string;
    config: State;
    setTitle: (d: string) => void;
    setConfig: (d: any) => void,
    setIten: (d: itenUpdate) => void
    itenUp: (d: number) => void
    itenDown: (d: number) => void
    addIten: () => void
    removeItem: (d: number) => void
}
export function Config({ title, config, setTitle, setConfig, setIten, iten, itenUp, itenDown, addIten, removeItem }: Props) {
    const { size, width, gap, margin, animated, color, text, opacidad, font } = config;
    return <Card style={{ backgroundColor: '#ccc', padding: '7px 7px 7px 7px' }}>
        <Row style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
            <Guardar guardar={() => { }} />
            <CardConfig width="287px">
                <RowConfig >
                    <Title setData={setTitle} title={title} width='281px' />
                </RowConfig>
                <RowConfig top={2}>
                    <Selector ani={animated} set={(animated) => setConfig({ animated })} />
                </RowConfig>
                <TitleCard>CONFIGURACION GENERAL</TitleCard>
            </CardConfig>
            <SizeViewSelector setSize={(size) => setConfig({ size })} size={size} />
            <TBElementLite />
            <CardConfig width="135px">
                <RowConfig >
                    <InputGroup style={{ height: 24, width: 128 }}>
                        <InputGroup.Prepend style={{ width: 75 }}>
                            MARGEN:
                        </InputGroup.Prepend>
                        <InputNumber max={width * .2} step={1} min={0} decimal={0} style={{ height: 24 }} value={margin} setData={margin => setConfig({ margin })} />
                    </InputGroup>
                </RowConfig>
                <RowConfig top={2}>
                    <InputGroup style={{ height: 24, width: 128 }}>
                        <ButtonGroup className='ml-auto mr-auto'>
                            <ButtonColor color={text} setData={(text) => setConfig({ text })}>
                                <i className="fas fa-paint-roller"></i>
                            </ButtonColor>
                            <ButtonColor color={font} setData={(font) => setConfig({ font })}>
                                <i className="fas fa-font"></i>
                            </ButtonColor>
                        </ButtonGroup>
                    </InputGroup>
                </RowConfig>
                <RowConfig top={2}>
                    <SetSlider max={99} min={0} setData={(o) => setConfig({ opacidad: o / 100 })} value={opacidad * 100} />
                </RowConfig>
                <TitleCard>CONTENEDOR DE TEXTO</TitleCard>
            </CardConfig>
            <CardConfig width="135px">
                <RowConfig >
                    <InputGroup style={{ height: 24, width: 128 }}>
                        <InputGroup.Prepend style={{ width: 75 }}>
                            CELDA:
                        </InputGroup.Prepend>
                        <InputNumber max={450} min={150} step={1} decimal={0} style={{ height: 24 }} value={width} setData={width => setConfig({ width })} />
                    </InputGroup>
                </RowConfig>
                <RowConfig top={2}>
                    <InputGroup style={{ height: 24, width: 128 }}>
                        <InputGroup.Prepend style={{ width: 75 }}>
                            ESPACIO:
                        </InputGroup.Prepend>
                        <InputNumber max={width * .1} step={1} min={0} decimal={0} style={{ height: 24 }} value={gap} setData={gap => setConfig({ gap })} />
                    </InputGroup>
                </RowConfig>
                <RowConfig top={2}>
                    <ButtonGroup>
                        <ButtonColor color={color} setData={(color) => setConfig({ color })}>
                            <i className="fas fa-paint-roller"></i>
                        </ButtonColor>
                    </ButtonGroup>
                    <AddItens add={addIten} />
                </RowConfig>
                <TitleCard>CUADRICULA</TitleCard>
            </CardConfig>
            <ConfigIten iten={iten} setConfig={setIten} itenUp={itenUp} itenDown={itenDown} removeItem={removeItem} />
        </Row >
    </Card >
}