import react, { useState, useContext, useEffect } from 'react';
import { Card, Row, Button, FormControl, InputGroup, ButtonGroup } from 'react-bootstrap';
import { Arriba, Abajo, Izquierda, Derecha, VentanaH, VentanaV, Remolino, Disolver, Zoom } from './variants'
import { animatedList } from './Itens/props';

export function Selector({ set, ani }: { set: (s: string) => void, ani: animatedList }) {
    const [page, setPage] = useState(getDefault(ani))
    return <ButtonGroup size="sm">
        <Page page={page} set={setPage} />
        {page === 1 ? <>
            <Button onClick={() => set('top')} active={(ani === 'top')} variant='outline-info'><Arriba /></Button>
            <Button onClick={() => set('bottom')} active={(ani === 'bottom')} variant='outline-info'><Abajo /></Button>
            <Button onClick={() => set('right')} active={(ani === 'right')} variant='outline-info'><Derecha /></Button>
        </> : page === 2 ? <>
            <Button onClick={() => set('left')} active={(ani === 'left')} variant='outline-info'><Izquierda /></Button>
            <Button onClick={() => set('rotate')} active={(ani === 'rotate')} variant='outline-info'><Remolino /></Button>
            <Button onClick={() => set('simple')} active={(ani === 'simple')} variant='outline-info'><Disolver /></Button>
        </> : <>
            <Button onClick={() => set('scaled')} active={(ani === 'scaled')} variant='outline-info'><Zoom /></Button>
            <Button onClick={() => set('scaledY')} active={(ani === 'scaledY')} variant='outline-info'><VentanaH /></Button>
            <Button onClick={() => set('scaledX')} active={(ani === 'scaledX')} variant='outline-info'><VentanaV /></Button>
        </>}
    </ButtonGroup >
}
function getDefault(ani: animatedList) {
    switch (ani) {
        case 'top':
        case 'bottom':
        case 'right':
            return 1;
        case 'left':
        case 'rotate':
        case 'simple':
            return 2;
        default:
            return 3;
    }

}
function Page({ set, page }: { set: (s: number) => void, page: number }) {
    return <ButtonGroup vertical size="sm">
        <Button style={{ padding: '0 0 0 0' }} onClick={() => set(page === 1 ? 3 : page - 1)} variant='outline-dark'>
            <i className="fas fa-arrow-up" />
        </Button>
        <Button style={{ padding: '0 0 0 0' }} onClick={() => set(page === 3 ? 1 : page + 1)} variant='outline-dark'>
            <i className="fas fa-arrow-down" />
        </Button>
    </ButtonGroup>

}