import React, { useState } from 'react'
import { Card, Row, Button, ButtonGroup, InputGroup, FormControl } from 'react-bootstrap';
import { css } from '@emotion/css';
import { InputText, InputNumber } from '../../../Utiles/Inputs';
interface Props {
  setSize: (d: string) => void
  size: string
}
export type sizeName = 'xs' | 'sm' | 'md' | 'lg' | 'xl' | 'xxl';
export type sizeWidth = '570px' | '780px' | '990px' | '1080px' | '1320px' | '1440px';
export function SizeViewSelector({ setSize, size }: Props) {
  return <Card style={{ backgroundColor: '#eee', margin: '0 0 0 5px', width: '115px' }}>
    <Card.Body style={{ margin: '0 0 0 0', padding: '3px 3px 3px 3px' }}>
      <Row className='d-flex' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
        <ButtonGroup size='sm'>
          <Button style={{ width: '36px' }} variant='outline-dark' active={size === '570px'} onClick={() => setSize('570px')}><i className="fas fa-mobile-alt"></i></Button>
          <Button style={{ width: '36px' }} variant='outline-dark' active={size === '780px'} onClick={() => setSize('780px')}><i className="fas fa-tablet-alt"></i></Button>
          <Button style={{ width: '36px' }} variant='outline-dark' active={size === '990px'} onClick={() => setSize('990px')}><i className="fas fa-mobile-alt fa-rotate-90"></i></Button>
        </ButtonGroup>
      </Row>
      <Row className='d-flex mt-2' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
        <ButtonGroup size='sm'>
          <Button style={{ width: '36px' }} variant='outline-dark' active={size === '1080px'} onClick={() => setSize('1080px')}><i className="fas fa-chalkboard"></i></Button>
          <Button style={{ width: '36px' }} variant='outline-dark' active={size === '1320px'} onClick={() => setSize('1320px')}><i className="fas fa-desktop"></i></Button>
          <Button style={{ width: '36px' }} variant='outline-dark' active={size === '1440px'} onClick={() => setSize('1440px')}><i className="fas fa-tv"></i></Button>
        </ButtonGroup>
      </Row>
      <Row className='d-flex mt-2' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
        <InputText disabled style={{ height: '24px' }} className='form-control-sm' setData={setSize} value={size} />
      </Row>
      <TitleCard>RESOLUCION</TitleCard>
    </Card.Body>
  </Card>
}


export function SizeViewConteiner({ size, children }: { size: string, children: any }) {

  return <div className='d-flex justify-content-center' style={{ height: 'calc(100vh - 280px)', }}>
    <Card style={{ height: 'calc(100vh - 285px)', width: size, border: '3px solid black', overflowY: 'scroll' }}>
      {children}
    </Card>
  </div>
}

export function Title({ setData, title, width = '355px' }: { setData: (d: string) => void, title: string, width?: string }) {
  return <InputGroup className='input-group-sm' style={{ width, height: '24px' }}>
    <InputGroup.Prepend style={{ height: '24px', width: '140px' }}>
      TITULO DEL PANEL
    </InputGroup.Prepend>
    <InputText style={{ height: '24px' }} className='form-control-sm' setData={setData} value={title} />
  </InputGroup>
}


export function ButtonImage({ setData }: { setData: (d: string) => void }) {
  const setImage = (e: React.ChangeEvent<HTMLInputElement>) => {
    let f = e.currentTarget.files
    let reader = new FileReader()
    reader.onloadend = () => setData(reader.result as string);
    if (f != null) reader.readAsDataURL(f[0])
  }
  return <Button variant='outline-dark' as='label' size='sm' style={{ margin: '0 0 0 0', width: 36, height: 24 }}>
    <i className="fas fa-image"></i>
    <input style={{ display: "none" }} onChange={setImage} type="file" />
  </Button>
}
interface colorProps {
  setData: (d: string) => void,
  color: string,
  children: any,
  disable?: boolean
  className?: string
}
export function ButtonColor({ className, setData, color, children, disable = false }: colorProps) {
  return <Button className={className} disabled={disable} as='label' variant={disable ? 'muted' : 'outline-dark'} style={{ margin: '0 0 0 0', width: 36, height: 24 }}>
    {children}
    {disable ? <></> : <>
      <div style={{ backgroundColor: color, width: 10, height: 10, position: 'absolute', top: 10, right: 5, zIndex: 3 }}></div>
      <input style={{ width: 0, height: 0, position: 'absolute', top: 17, right: 13, zIndex: -3 }} value={color} type='color'
        onChange={(e: React.ChangeEvent<HTMLInputElement>) => setData(e.currentTarget.value)} />
    </>}
  </Button>
}

export function Guardar({ guardar }: { guardar: () => void }) {
  return <Button variant='default' onClick={guardar} className={css`
    color: #28a745;
    padding: 1rem 1rem 1rem 1rem;
    border-color: #28a745;
    &:hover{
      color: white;
      background-color: #28a745;
    }`} >
    <i className="fa fa-save fa-4x"></i>
  </Button>
}
export function TitleCard({ children }: { children: any }) {
  return <Row className='d-flex justify-content-center mt-1' style={{ backgroundColor: '#fff', margin: '0 -3px -3px -3px', padding: '0 0 0 0', borderRadius: '0 0 .25rem .25rem', }}>
    <small style={{ fontSize: 9 }}>{children}</small>
  </Row>
}
export function CardConfig({ children, width }: { children: any, width: string }) {
  return <Card style={{ backgroundColor: '#eee', margin: '0 0 0 5px', width }}>
    <Card.Body style={{ margin: '0 0 0 0', padding: '3px 3px 3px 3px' }}>
      {children}
    </Card.Body>
  </Card>
}
export function RowConfig({ children, top }: { children: any, top?: number }) {
  return <Row className={top ? 'mt-' + top : undefined} style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
    {children}
  </Row>
}
export function SetSlider({ setData, value, max, min }: { setData: (d: number) => void, value: number, max?: number, min?: number }) {
  const setDataSelected = (e: React.ChangeEvent<HTMLSelectElement>) => setData(parseInt(e.currentTarget.value));
  return <InputGroup style={{ height: 24, width: '100%' }}>
    <InputGroup.Prepend style={{ width: 43 }}>
      <InputNumber decimal={0} style={{ height: 24 }} value={value} setData={setData} />
    </InputGroup.Prepend>
    <InputGroup.Append style={{ height: 24, width: 'calc(100% - 43px)' }}>
      <InputGroup.Text style={{ height: 24, width: '100%' }}>
        <FormControl onChange={setDataSelected} value={value}
          type="range" max={max} min={min} />
      </InputGroup.Text>
    </InputGroup.Append>
  </InputGroup>
}

export function AddItens({ add }: { add: () => void }) {
  const [cont, setCont] = useState(1);
  const action = () => {
    for (let i = 0; i < cont; i++)add();
    setCont(1)
  }
  return <InputGroup className='ml-auto' style={{ height: 24, width: 79 }}>
    <InputGroup.Prepend style={{ width: 43 }}>
      <InputNumber decimal={0} style={{ height: 24 }} value={cont} setData={setCont} />
    </InputGroup.Prepend>
    <InputGroup.Append style={{ height: 24 }}>
      <Button onClick={action} variant='outline-dark' size='sm' style={{ width: 36, height: 24 }}>
        <i className="fas fa-plus"></i>
      </Button>
    </InputGroup.Append>
  </InputGroup>
}