import react, { useContext } from 'react';
import { getCSS, box } from './props';
import { EditArea, AlingContext } from '../../SlateCustom'
interface Props {
    config: {
        image: string;
        color: string;
        ori: boolean;
        variant: 'diagonal' | 'arrow' | 'semi-circle' | 'diagonal-i' | 'arrow-i' | '';
        size: string
    }
}
export function Panel({ config: { image, color, ori, variant, size } }: Props) {
    const { HAlign } = useContext(AlingContext);
    return <div style={{ backgroundColor: color }} className={box}>
        <div className={getCSS(ori, color, image, size)}>
            <div className={`c-bg-parallax c-feature-bg c-content c-${variant}`}></div>
            <div className="c-content-area c-content-ori"></div>
            <div className="container">
                <div className="c-feature-content c-ori">
                    <div className="c-content-v-center">
                        <div className="c-wrapper">
                            <div className={`c-body text-${HAlign}`}>
                                <EditArea />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
}