import React from 'react';
import { Panel } from './Panel';
import { Config } from './Config';
import SlateProvider, { HA } from '../../SlateCustom';
import { SizeViewConteiner } from '../../Crear/component';
export interface area {
    span: number
    offset: number
    marginTop: number
    marginBottom: number
    height: number
}
export interface State {
    size: string
    image: string;
    color: string;
    opacity: number;
    align: HA
    xs: area
    sm: area
    md: area
    lg: area
    xl: area
    xxl: area
}
export class Paralax2 extends React.Component<{}, State> {
    title = '';
    texto: any[] = [{
        type: 'paragraph',
        children: [
            { text: 'This is ', 'font:Arial': true, '#000000': true },
            { text: 'editable', bold: true, 'font:Arial': true, '#000000': true },
            { text: ' plain text, just like a ', 'font:Arial': true, '#000000': true },
            { text: ' <textarea>!', bold: true, 'font:Arial': true, '#000000': true },
        ],
    }];
    constructor(p: {}) {
        super(p);
        this.state = {
            size: '1440px',
            image: '/public/panels/default.jpg',
            color: '#ffffff',
            align: 'left',
            opacity: 0.7,
            xs: {
                span: 100, offset: 0,
                marginTop: 0,
                marginBottom: 0,
                height: 5,
            },
            sm: {
                span: 80, offset: 10,
                marginTop: 0,
                marginBottom: 0,
                height: 5,
            },
            md: {
                span: 33, offset: 66,
                marginTop: 0,
                marginBottom: 0,
                height: 5,
            },
            lg: {
                span: 30, offset: 70,
                marginTop: 0,
                marginBottom: 0,
                height: 5,
            },
            xl: {
                span: 70, offset: 30,
                marginTop: 0,
                marginBottom: 0,
                height: 5,
            },
            xxl: {
                span: 70, offset: 30,
                marginTop: 0,
                marginBottom: 0,
                height: 5,
            },
        };
        this.setState = this.setState.bind(this);
        this.setTitle = this.setTitle.bind(this);
        this.setTexto = this.setTexto.bind(this);
    }
    setTitle(d: string) { this.title = d; }
    setTexto(d: any[]) { this.texto = d; }
    render() {
        return <div style={{ height: '100%', maxHeight: 'calc(100vh - 180px)' }}>
            <SlateProvider value={this.texto} setValue={this.setTexto}>
                <Config setConfig={this.setState} title={this.title} setTitle={this.setTitle} config={this.state} />
                <SizeViewConteiner size={this.state.size}>
                    <Panel config={this.state} />
                </SizeViewConteiner>
            </SlateProvider>
        </div>
    }
}
