import react, { useState, useContext, useEffect } from 'react';
import { Card, Row, Button, FormControl, InputGroup, ButtonGroup } from 'react-bootstrap';
import { InputNumber, InputText } from '../../../../Utiles/Inputs';
import { SizeViewSelector, Title, ButtonColor, TitleCard, Guardar } from '../../Crear/component';
import { TBElement, AlingContext } from '../../SlateCustom'
import { State } from './index'
interface Props { title: string; config: State; setTitle: (d: string) => void; setConfig: (d: any) => void }
type sel = 'span' | 'offset' | 'mt' | 'mb' | 'height' | 'opacity'
export function Config({ title, config, setTitle, setConfig }: Props) {
    const { size, backgroundColor, textColor, text, aspect, xs, sm, md, lg, xl, xxl, video, ori } = config;
    const { HAlign } = useContext(AlingContext);
    const setAspect = (e: React.ChangeEvent<HTMLSelectElement>) => setConfig({ aspect: e.currentTarget.value });
    const setConfigSelected = (e: React.ChangeEvent<HTMLSelectElement>) => setSpan(parseInt(e.currentTarget.value));
    const setSpan = (p: number) => {
        const l = (size === '570px') ? 'xs' :
            (size === '780px') ? 'sm' :
                (size === '990px') ? 'md' :
                    (size === '1080px') ? 'lg' :
                        (size === '1320px') ? 'xl' : 'xxl';
        setConfig({ [l]: p });
    }
    const getConfig = () => {
        return (size === '570px') ? xs :
            (size === '780px') ? sm :
                (size === '990px') ? md :
                    (size === '1080px') ? lg :
                        (size === '1320px') ? xl : xxl;
    }
    const setVideo = (video: string) => {
        if (video.indexOf('youtube') > -1)
            video = video.replace('watch?v=', 'embed/');
        setConfig({ video })
    }
    useEffect(() => { setConfig({ align: HAlign }) }, [HAlign]);
    return <Card style={{ backgroundColor: '#ccc', padding: '7px 7px 7px 7px' }}>
        <Row style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
            <Guardar guardar={() => { }} />
            <Card style={{ backgroundColor: '#eee', margin: '0 0 0 5px', width: '420px' }}>
                <Card.Body style={{ margin: '0 0 0 0', padding: '3px 3px 3px 3px' }}>
                    <Row style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                        <Title setData={setTitle} title={title} width='298px' />
                        <Button onClick={() => setConfig({ text: !text })} active={text} className='ml-1' variant='outline-dark' style={{ padding: '.25rem .5rem .25rem .5rem', margin: '0 0 0 0', width: 36, height: 24 }}>
                            <i className="fas fa-align-justify"></i>
                        </Button>
                        <ButtonGroup size='sm' className='ml-1'>
                            <ButtonColor disable={!text} color={textColor} setData={(textColor) => setConfig({ textColor })}>
                                <i className="fas fa-font"></i>
                            </ButtonColor>
                            <ButtonColor color={backgroundColor} setData={(backgroundColor) => setConfig({ backgroundColor })}>
                                <i className="fas fa-paint-roller"></i>
                            </ButtonColor>
                        </ButtonGroup>
                    </Row>
                    <Row className='mt-2' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                        <FormControl style={{ height: '24px', width: '65px', padding: '0 0 0 0' }} className='form-control-sm' as='select' onChange={setAspect} value={aspect}>
                            <option key='16/9' value='16/9'>16:9</option>
                            <option key='4/3' value='4/3'>4:3</option>
                            <option key='1/1' value='1/1'>1:1</option>
                            <option key='3/2' value='3/2'>3:2</option>
                            <option key='9/16' value='9/16'>9:16</option>
                        </FormControl>
                        <InputGroup className='ml-1' style={{ height: 24, width: 270 }}>
                            <InputGroup.Prepend style={{ width: 45 }}>
                                <InputNumber decimal={0} disabled={!text} style={{ height: 24 }} value={getConfig()} setData={(n) => setSpan(n)} />
                            </InputGroup.Prepend>
                            <InputGroup.Append style={{ height: 24, width: 225 }}>
                                <InputGroup.Text style={{ height: 24, width: '100%' }}>
                                    <FormControl disabled={!text} onChange={setConfigSelected} value={getConfig()} type="range" max={71} min={40} />
                                </InputGroup.Text>
                            </InputGroup.Append>
                        </InputGroup>
                        <ButtonGroup size='sm' className='ml-1'>
                            <Button as='label' active={ori} onClick={() => setConfig({ ori: true })} variant={text ? 'outline-dark' : 'muted'} style={{ margin: '0 0 0 0', height: '24px', width: '35px' }}>
                                <i className="fas fa-caret-square-left"></i>
                            </Button>
                            <Button as='label' active={!ori} onClick={() => setConfig({ ori: false })} variant={text ? 'outline-dark' : 'muted'} style={{ margin: '0 0 0 0', height: '24px', width: '35px' }}>
                                <i className="fas fa-caret-square-right"></i>
                            </Button>
                        </ButtonGroup>
                    </Row>
                    <Row className='mt-2' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                        <InputGroup className='input-group-sm' style={{ height: '24px' }}>
                            <InputGroup.Prepend style={{ height: '24px' }}>
                                <InputGroup.Text style={{ height: '24px' }}>LINK</InputGroup.Text>
                            </InputGroup.Prepend>
                            <InputText style={{ height: '24px' }} className='form-control-sm' setData={setVideo} value={video} />
                        </InputGroup>
                    </Row>
                    <TitleCard>CONFIGURACION GENERAL</TitleCard>
                </Card.Body>
            </Card>
            <SizeViewSelector setSize={(d) => setConfig({ size: d })} size={size} />
            <TBElement />
        </Row >
    </Card >
}