import react, { useContext } from 'react';
import { content, imagen } from './props';
import SlateProvider, { EditArea } from '../../../SlateCustom';
import HoveringToolbar from '../../../SlateCustom/hoverMenu';
import { iten, State } from '../index';

export function Itens({ config: { margin, padding, animated, seleted, text, opacidad, font }, iten: { id, html, img, col, row }, setText, seled }: { iten: iten, config: State, setText: (d: any) => void, seled: (i: number) => void }) {
    const seletedMe = () => {
        seled(id);
        setTimeout(() => {
            const div = document.getElementById("selectedMe")
            if (div) div.scrollIntoView();
        }, 10)
    }
    return <div id={seleted === id ? 'selectedMe' : undefined} onClick={seletedMe} className={content(text, font, opacidad, margin, padding, animated, col, row, seleted === id)}>
        <img style={imagen} alt="Bricks" src={img} />
        {seleted === id ?
            <div className='caption text-center'> <EditArea key={'edit' + id} /></div> :
            <div className='caption text-center' key={'html' + id} dangerouslySetInnerHTML={{ __html: html }} />
        }
    </div >
}