import react, { useState, useContext, useEffect } from 'react';
import { Card, Row, Button, FormControl, InputGroup, ButtonGroup } from 'react-bootstrap';
import { InputNumber } from '../../../../Utiles/Inputs';
import { SizeViewSelector, Title, ButtonImage, ButtonColor, TitleCard, Guardar, SetSlider } from '../../Crear/component';
import { TBElement, AlingContext } from '../../SlateCustom'
import { State, area } from './index'
import { Background } from './variants'


interface Props {
    title: string
    config: State
    setTitle: (d: string) => void
    setConfig: (d: any) => void
}
type sel =
    'span' |
    'offset' |
    'mt' |
    'mb' |
    'height' |
    'opacity'
export function Config({ title, config, setTitle, setConfig }: Props) {
    const [selected, setSelected] = useState<sel>('offset');
    const { color, xs, sm, md, lg, xl, xxl, opacity, size } = config;
    const setSpan = (p: area) => {
        const l = (size === '570px') ? 'xs' :
            (size === '780px') ? 'sm' :
                (size === '990px') ? 'md' :
                    (size === '1080px') ? 'lg' :
                        (size === '1320px') ? 'xl' : 'xxl';
        setConfig({ [l]: p });
    }
    const setConfig2 = (n: number) => {
        const l = (size === '570px') ? xs :
            (size === '780px') ? sm :
                (size === '990px') ? md :
                    (size === '1080px') ? lg :
                        (size === '1320px') ? xl : xxl;
        switch (selected) {
            case 'span': setSpan({ ...l, span: n }); break;
            case 'offset': setSpan({ ...l, offset: n }); break;
            case 'mt': setSpan({ ...l, marginTop: n }); break;
            case 'mb': setSpan({ ...l, marginBottom: n }); break;
            case 'height': setSpan({ ...l, height: n }); break;
            case 'opacity': setConfig({ opacity: n / 100 }); break;
            default: setConfig({ [selected]: n }); break;
        }

    }
    const setConfigSelected = (e: React.ChangeEvent<HTMLSelectElement>) => setConfig2(parseInt(e.currentTarget.value));
    const getConfig = () => {
        const l = (size === '570px') ? xs :
            (size === '780px') ? sm :
                (size === '990px') ? md :
                    (size === '1080px') ? lg :
                        (size === '1320px') ? xl : xxl;
        switch (selected) {
            case 'span': return l.span;
            case 'offset': return l.offset;
            case 'mt': return l.marginTop;
            case 'mb': return l.marginBottom;
            case 'height': return l.height;
            case 'opacity': return opacity * 100;
            default: return config[selected];
        }
    }
    const getLimit = () => {
        switch (selected) {
            case 'span': return { min: 25, max: 99 };
            case 'offset': return { min: 0, max: 75 };
            case 'height': return { min: 5, max: 99 };
            default: return { min: 0, max: 99 };
        }
    }
    const { HAlign } = useContext(AlingContext);
    useEffect(() => { setConfig({ align: HAlign }) }, [HAlign]);
    return <Card style={{ backgroundColor: '#ccc', padding: '7px 7px 7px 7px' }}>
        <Row style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
            <Guardar guardar={() => { }} />
            <Card style={{ backgroundColor: '#eee', margin: '0 0 0 5px', width: '302px' }}>
                <Card.Body style={{ margin: '0 0 0 0', padding: '3px 3px 3px 3px' }}>
                    <Row className='d-flex' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                        <Title setData={setTitle} title={title} width='298px' />
                    </Row>
                    <Row className='d-flex mt-1' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                        <ButtonGroup size='sm' >
                            <ButtonImage setData={(image) => setConfig({ image })} />
                            <ButtonColor color={color} setData={(color) => setConfig({ color })}>
                                <i className="fas fa-paint-roller"></i>
                            </ButtonColor>
                        </ButtonGroup>
                        <Button active={selected === 'opacity'} className='ml-1' onClick={() => setSelected('opacity')} variant='outline-dark' style={{ margin: '0 0 0 0', paddingTop: '.20rem', width: 36, height: 24 }}>
                            <Background></Background>
                        </Button>
                        <ButtonGroup className='ml-1' size='sm'>
                            <Button active={selected === 'offset'} onClick={() => setSelected('offset')} variant='outline-dark' style={{ margin: '0 0 0 0', width: 36, height: 24 }}>
                                <i style={{ borderRight: '2px currentColor solid' }} className="fas fa-arrow-right"></i>
                            </Button>
                            <Button active={selected === 'mt'} onClick={() => setSelected('mt')} variant='outline-dark' style={{ margin: '0 0 0 0', width: 36, height: 24 }}>
                                <i style={{ borderBottom: '2px currentColor solid' }} className="fas fa-arrow-down"></i>
                            </Button>
                            <Button active={selected === 'mb'} onClick={() => setSelected('mb')} variant='outline-dark' style={{ margin: '0 0 0 0', width: 36, height: 24 }}>
                                <i style={{ borderTop: '2px currentColor solid' }} className="fas fa-arrow-up"></i>
                            </Button>
                            <Button active={selected === 'span'} onClick={() => setSelected('span')} variant='outline-dark' style={{ margin: '0 0 0 0', width: 36, height: 24 }}>
                                <i className="fas fa-arrows-alt-h"></i>
                            </Button>
                            <Button active={selected === 'height'} onClick={() => setSelected('height')} variant='outline-dark' style={{ margin: '0 0 0 0', width: 36, height: 24 }}>
                                <i className="fas fa-arrows-alt-v"></i>
                            </Button>
                        </ButtonGroup>
                    </Row>
                    <Row className='d-flex mt-1' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                        <SetSlider value={getConfig()} setData={setConfig2} max={getLimit().max} min={getLimit().min} />
                    </Row>
                    <TitleCard>CONFIGURACION GENERAL</TitleCard>
                </Card.Body>
            </Card>
            <SizeViewSelector setSize={(d) => setConfig({ size: d })} size={size} />
            <TBElement />
        </Row >
    </Card >
}