import react, { useContext } from 'react';
import { getCSS } from './props';
import { EditArea, AlingContext } from '../../SlateCustom'
import { Row, Container } from 'react-bootstrap';
import { State } from './index'
import { Itens } from './Itens'

export function Panel({ config, setText, seled }: { config: State, setText: (d: any) => void, seled: (i: number) => void }) {
    const { color, itens, width, gap } = config;
    return <Row style={{ backgroundColor: color, margin: '0 0 0 0', padding: '0 0 0 0' }}>
        <Container className={getCSS(width, gap)}>
            {itens.map((i) => <Itens iten={i} setText={setText} config={config} seled={seled} />)}
        </Container>
    </Row>
}