import React from 'react';
import { Panel } from './Panel';
import { Config } from './Config';
import SlateProvider, { HA } from '../../SlateCustom';
import { SizeViewConteiner } from '../../Crear/component';
export interface State {
    size: string
    align: HA
    video: string;
    backgroundColor: string;
    textColor: string;
    ori: boolean;
    aspect: '1/1' | '3/2' | '4/3' | '16/9' | '9/16';
    text: boolean;
    xs: number;
    sm: number;
    md: number;
    lg: number;
    xl: number;
    xxl: number;
}
export class Video extends React.Component<{}, State> {
    title = '';
    texto: any[] = [{
        type: 'paragraph',
        children: [
            { text: 'This is ', 'font:Arial': true, '#000000': true },
            { text: 'editable', bold: true, 'font:Arial': true, '#000000': true },
            { text: ' plain text, just like a ', 'font:Arial': true, '#000000': true },
            { text: ' <textarea>!', bold: true, 'font:Arial': true, '#000000': true },
        ],
    }];
    constructor(p: {}) {
        super(p);
        this.state = {
            ori: false, text: true, size: '1440px', align: 'left', video: 'https://www.youtube.com/embed/jyQXHZrQEbQ', backgroundColor: '#f0f0f0', textColor: '#ffffff', aspect: '16/9',
            xs: 100, sm: 100, md: 70, lg: 60, xl: 50, xxl: 50,
        };
        this.setState = this.setState.bind(this);
        this.setTitle = this.setTitle.bind(this);
        this.setTexto = this.setTexto.bind(this);
    }
    setTitle(d: string) { this.title = d; }
    setTexto(d: any[]) { this.texto = d; }
    render() {
        return <div style={{ height: '100%', maxHeight: 'calc(100vh - 180px)' }}>
            <SlateProvider value={this.texto} setValue={this.setTexto}>
                <Config setConfig={this.setState} title={this.title} setTitle={this.setTitle} config={this.state} />
                <SizeViewConteiner size={this.state.size}>
                    <Panel config={this.state} />
                </SizeViewConteiner>
            </SlateProvider>
        </div>
    }
}
