import react, { useState, useContext, useEffect } from 'react';
import { TitleCard, CardConfig, RowConfig, ButtonImage } from '../../../Crear/component';
import { InputNumber, InputText } from '../../../../../Utiles/Inputs';
import { Card, InputGroup, Row, Button, ButtonGroup } from 'react-bootstrap';
import { iten, itenUpdate } from '../index'
export function ConfigIten({ iten: { row, col, id }, setConfig, itenUp, itenDown, removeItem }: { iten: iten, setConfig: (d: itenUpdate) => void, itenUp: (d: number) => void, itenDown: (d: number) => void, removeItem: (d: number) => void }) {
    return <CardConfig width='145px'>
        <RowConfig>
            <InputGroup style={{ height: 24 }}>
                <InputGroup.Prepend style={{ width: 95 }}>
                    FILAS:
                </InputGroup.Prepend>
                <InputNumber max={7} min={1} decimal={0} style={{ height: 24 }} value={row} setData={row => setConfig({ row, id })} />
            </InputGroup>
        </RowConfig>
        <RowConfig top={2}>
            <InputGroup style={{ height: 24 }}>
                <InputGroup.Prepend style={{ width: 95 }}>
                    COLUMNAS:
                </InputGroup.Prepend>
                <InputNumber max={7} min={1} decimal={0} style={{ height: 24 }} value={col} setData={col => setConfig({ col, id })} />
            </InputGroup>
        </RowConfig>
        <RowConfig top={2}>
            <ButtonImage setData={img => setConfig({ img, id })} />
            <ButtonGroup className='ml-1'>
                <Button size='sm' style={{ width: 32, height: 24 }} onClick={() => itenUp(id)} variant='outline-dark'>
                    <i className="fas fa-arrow-up" />
                </Button>
                <Button size='sm' style={{ width: 32, height: 24 }} onClick={() => itenDown(id)} variant='outline-dark'>
                    <i className="fas fa-arrow-down" />
                </Button>
            </ButtonGroup>
            <Button className='ml-1' size='sm' style={{ width: 32, height: 24 }} onClick={() => removeItem(id)} variant='outline-dark'>
                <i className="fas fa-trash" />
            </Button>
        </RowConfig>
        <TitleCard>IMAGEN {id}</TitleCard>
    </CardConfig>
}
