import React from 'react';
import { Panel } from './Panel';
import { Config } from './Config';
import { SizeViewConteiner } from '../../Crear/component';
import { animatedList } from './Itens/props';
import SlateProvider, { HA } from '../../SlateCustom';

export interface iten {
    id: number;
    img: string;
    html: string;
    text: any[];
    col: number;
    row: number;
}
export interface itenUpdate {
    id?: number;
    img?: string;
    text?: any[];
    html?: string;
    col?: number;
    row?: number;
}
export interface State {
    size: string
    color: string;
    text: string;
    font: string;
    opacidad: number;
    width: number;
    gap: number;
    seleted: number;
    margin: number;
    padding: number;
    animated: animatedList;
    itens: iten[];
}
export const defaultIten = {
    img: '/public/panels/iten.jpg', col: 1, row: 1,
    html: '<p data-slate-node="element" style="width: 100%;"><span data-slate-node="text"><span data-slate-leaf="true" style="font-family: Arial;"><span data-slate-string="true">This is </span></span></span><span data-slate-node="text"><span data-slate-leaf="true" style="font-family: Arial;"><strong><span data-slate-string="true">editable</span></strong></span></span><span data-slate-node="text"><span data-slate-leaf="true" style="font-family: Arial;"><span data-slate-string="true"> plain text, just like a </span></span></span><span data-slate-node="text"><span data-slate-leaf="true" style="font-family: Arial;"><strong><span data-slate-string="true"> &lt;textarea&gt;!</span></strong></span></span></p>',
    text: [{
        type: 'paragraph',
        children: [
            { text: 'This is ', 'font:Arial': true },
            { text: 'editable', bold: true, 'font:Arial': true },
            { text: ' plain text, just like a ', 'font:Arial': true },
            { text: ' <textarea>!', bold: true, 'font:Arial': true },
        ],
    }]
}
export class Galeria extends React.Component<{}, State> {
    title = '';
    constructor(p: {}) {
        super(p);
        this.state = {
            seleted: 1, size: '1440px', color: '#f0f0f0', text: '#000000', font: '#FFFFFF', opacidad: .6,
            width: 180, gap: 5, margin: 10, padding: 5, animated: 'simple',
            itens: [
                { id: 1, ...defaultIten, row: 3, col: 4 },
                { id: 2, ...defaultIten, row: 2 },
                { id: 3, ...defaultIten },
                { id: 4, ...defaultIten },
                { id: 5, ...defaultIten, col: 2 }
            ]
        };
        this.setState = this.setState.bind(this);
        this.setTitle = this.setTitle.bind(this);
        this.setTexto = this.setTexto.bind(this);
        this.itenUp = this.itenUp.bind(this);
        this.itenDown = this.itenDown.bind(this);
        this.addIten = this.addIten.bind(this);
        this.removeItem = this.removeItem.bind(this);
    }
    setTitle(d: string) { this.title = d; }
    setTexto({ text, img, id, col, row }: itenUpdate) {
        const { itens } = this.state;
        let html: any = undefined;
        if (text) {
            const div = document.getElementById('EditableHTML');
            html = div ? div.innerHTML : undefined;
        }
        this.setState({
            itens: itens.map(p => {
                if (p.id === id) {
                    return {
                        id,
                        text: text ? text : p.text,
                        img: img ? img : p.img,
                        col: col ? col : p.col,
                        row: row ? row : p.row,
                        html: html ? html : p.html,
                    };
                }
                return p;
            })
        })
    }
    addIten() {
        const { itens } = this.state;
        const ids = itens.map(({ id }) => id);
        const id = Math.max(...ids) + 1;
        this.setState({ itens: [...itens, { id, ...defaultIten }] })
    }
    removeItem(id: number) {
        const { itens } = this.state;
        this.setState({
            itens: itens
                .filter(i => i.id !== id)
                .map((a, i) => { a.id = i + 1; return a }),
            seleted: 1
        })
    }
    itenUp(id: number) {
        const { itens } = this.state;
        if (id !== 1) {
            const rest = itens[id - 2];
            itens[id - 2] = itens[id - 1];
            itens[id - 1] = rest;
        }
        this.setState({
            itens: itens.map((a, i) => { a.id = i + 1; return a }),
            seleted: id === 1 ? 1 : id - 1
        })
    }
    itenDown(id: number) {
        const { itens } = this.state;
        if (id !== itens.length) {
            const rest = itens[id];
            itens[id] = itens[id - 1];
            itens[id - 1] = rest;
        }
        this.setState({
            itens: itens.map((a, i) => { a.id = i + 1; return a }),
            seleted: id === itens.length ? id : id + 1
        })
    }
    seleted(seleted: number) {
        this.setState({ seleted })
    }
    getIten() {
        const { itens, seleted } = this.state;
        for (let i = 0; i < itens.length; i++)
            if (seleted === itens[i].id)
                return itens[i];
        return { id: 0, ...defaultIten };
    }
    setIten({ text, img, col, row }: itenUpdate) {
        const { itens, seleted } = this.state;
        let html: any = undefined;
        if (text) {
            const div = document.getElementById('EditableHTML');
            html = div ? div.innerHTML : undefined;
        }
        this.setState({
            itens: itens.map(p => {
                if (p.id === seleted) {
                    return {
                        id: seleted,
                        text: text ? text : p.text,
                        img: img ? img : p.img,
                        col: col ? col : p.col,
                        row: row ? row : p.row,
                        html: html ? html : p.html,
                    };
                }
                return p;
            })
        })
    }

    render() {
        const { text, id } = this.getIten();
        return <div style={{ height: '100%', maxHeight: 'calc(100vh - 180px)' }}>
            <SlateProvider key={'stale' + id} value={text} setValue={(text) => this.setIten({ text })}>
                <Config
                    key='config'
                    iten={this.getIten()}
                    setIten={this.setTexto}
                    setConfig={this.setState}
                    title={this.title}
                    setTitle={this.setTitle}
                    removeItem={this.removeItem}
                    itenUp={this.itenUp}
                    itenDown={this.itenDown}
                    addIten={this.addIten}
                    config={this.state} />
                <SizeViewConteiner size={this.state.size}>
                    <Panel key='panel' config={this.state} setText={this.setTexto} seled={this.seleted.bind(this)} />
                </SizeViewConteiner>
            </SlateProvider>
        </div>
    }
}
