import react, { useContext, useEffect } from 'react';
import { Card, Row, Button, ButtonGroup } from 'react-bootstrap';
import { SizeViewSelector, Title, ButtonImage, ButtonColor, TitleCard, Guardar } from '../../Crear/component';
import { Arrow1, Arrow2, Circle, Dia1, Dia2, Ori } from './variants';
import { TBElement, AlingContext } from '../../SlateCustom'
import { State } from './index'
interface Props {
    title: string
    config: State
    setTitle: (d: string) => void
    setConfig: (d: any) => void
}
export function Config({ title, config: { color, ori, variant, size }, setTitle, setConfig }: Props) {
    const setImage = (e: React.ChangeEvent<HTMLInputElement>) => {
        let f = e.currentTarget.files
        let reader = new FileReader()
        reader.onloadend = function () {
            setConfig({ image: reader.result as string })
        }
        if (f != null)
            reader.readAsDataURL(f[0])
    }
    const { HAlign } = useContext(AlingContext);
    useEffect(() => { setConfig({ align: HAlign }) }, [HAlign]);
    return <Card style={{ backgroundColor: '#ccc', padding: '7px 7px 7px 7px' }}>
        <Row style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
            <Guardar guardar={() => { }}/>
            <Card style={{ backgroundColor: '#eee', margin: '0 0 0 5px', width: '543px' }}>
                <Card.Body style={{ margin: '0 0 0 0', padding: '3px 3px 3px 3px' }}>
                    <Row className='d-flex' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                        <Title setData={setTitle} title={title} width='395px' />
                        <ButtonGroup size='sm' className='ml-1'>
                            <ButtonImage setData={(image) => setConfig({ image })} />
                            <ButtonColor color={color} setData={(color) => setConfig({ color })}>
                                <i className="fas fa-paint-roller"></i>
                            </ButtonColor>
                        </ButtonGroup>
                        <ButtonGroup size='sm' className='ml-1'>
                            <Button as='label' onClick={() => setConfig({ ori: false })} active={!ori} variant='outline-info'>
                                <i className="fas fa-caret-square-left"></i>
                            </Button>
                            <Button as='label' onClick={() => setConfig({ ori: true })} active={ori} variant='outline-info'>
                                <i className="fas fa-caret-square-right"></i>
                            </Button>
                        </ButtonGroup>
                    </Row>
                    <Row className='d-flex' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                        <ButtonGroup size="sm">
                            <Button onClick={() => setConfig({ variant: '' })} active={(variant === '')} variant='outline-info'><Ori /></Button>
                            <Button onClick={() => setConfig({ variant: 'diagonal' })} active={(variant === 'diagonal')} variant='outline-info'><Dia2 /></Button>
                            <Button onClick={() => setConfig({ variant: 'diagonal-i' })} active={(variant === 'diagonal-i')} variant='outline-info'><Dia1 /></Button>
                            <Button onClick={() => setConfig({ variant: 'arrow' })} active={(variant === 'arrow')} variant='outline-info'><Arrow1 /></Button>
                            <Button onClick={() => setConfig({ variant: 'arrow-i' })} active={(variant === 'arrow-i')} variant='outline-info'><Arrow2 /></Button>
                            <Button onClick={() => setConfig({ variant: 'semi-circle' })} active={(variant === 'semi-circle')} variant='outline-info'><Circle /></Button>
                        </ButtonGroup>
                    </Row>
                    <TitleCard>CONFIGURACION GENERAL</TitleCard>
                </Card.Body>
            </Card>
            <SizeViewSelector setSize={(d) => setConfig({ size: d })} size={size} />
            <TBElement />
        </Row>
    </Card>
}