import React from 'react';
import { Card } from 'react-bootstrap';
import { Paralax1 } from '../EditPanel/Paralax1';
import { Paralax2 } from '../EditPanel/Paralax2';
import { Video } from '../EditPanel/Video';
import { Galeria } from '../EditPanel/Galeria';
import { Route } from "react-router-dom"
import Selector from './Selector';

export function Crear() {
    return <>
        <Route path="/panel/paralax1" exact component={Paralax1} />
        <Route path="/panel/paralax2" exact component={Paralax2} />
        <Route path="/panel/video" exact component={Video} />
        <Route path="/panel/galeria" exact component={Galeria} />
        <Route path="/panel" exact component={Selector} />
    </>
}