import React, { useCallback, useMemo, createContext, useContext, useState, useEffect, CSSProperties } from 'react'
import isHotkey from 'is-hotkey'
import { Editable, withReact, useSlate, Slate, ReactEditor } from 'slate-react'
import { Editor, Transforms, createEditor, Element as SlateElement, } from 'slate'
import { withHistory, HistoryEditor } from 'slate-history';
import { ButtonGroup, FormControl, Row, Card } from 'react-bootstrap';
import { TitleCard } from '../Crear/component'
import { Button } from './Component'
import { fontsFamily } from './FontsFamily';

type hkey = 'ctrl+b' | 'ctrl+i' | 'ctrl+u';
const HOTKEYS = {
    'ctrl+b': 'bold',
    'ctrl+i': 'italic',
    'ctrl+u': 'underline',
}
export type HA = 'left' | 'right' | 'center' | 'justify';
interface AlingState {
    HAlign: HA,
    setHAlign: (d: HA) => void,
}
const LIST_TYPES = ['numbered-list', 'bulleted-list']
export const SlateContext = createContext<ReactEditor & HistoryEditor | null>(null);
export const AlingContext = createContext<AlingState>({ HAlign: 'left', setHAlign: (d: string) => { } });
const SlateProvider = ({ value, setValue, children }: { value: any, setValue: (d: any) => void, children: any }) => {
    const editor = useMemo(() => withHistory(withReact(createEditor() as ReactEditor)), []);
    const [HAlign, setHAlign] = useState<HA>('left');
    return <Slate editor={editor} value={value} onChange={value => setValue(value)}>
        <AlingContext.Provider value={{ HAlign, setHAlign }} >
            <SlateContext.Provider value={editor}>
                {children}
            </SlateContext.Provider>
        </AlingContext.Provider>
    </Slate>
}

export const EditArea = ({ change, value }: { change?: (d: any) => void, value?: any[] }) => {
    const renderElement = useCallback(props => <Element {...props} />, [])
    const renderLeaf = useCallback(props => <Leaf {...props} />, [])
    let editor = useContext(SlateContext);
    return <Editable
        id='EditableHTML'
        renderElement={renderElement}
        renderLeaf={renderLeaf}
        placeholder="Enter some rich text…"
        spellCheck
        onChange={change} value={value}
        onKeyDown={(event: any) => {
            for (const hotkey in HOTKEYS) {
                if (isHotkey(hotkey, event as any)) {
                    let d = hotkey as hkey
                    event.preventDefault()
                    const mark = HOTKEYS[d]
                    toggleMark(editor, mark)
                }
            }
        }}
    />
}
export const TBElement = () => {
    let { HAlign, setHAlign } = useContext(AlingContext);
    return <Card style={{ backgroundColor: '#eee', margin: '0 0 0 5px', width: '265px' }}>
        <Card.Body style={{ margin: '0 0 0 0', padding: '3px 3px 3px 3px' }}>
            <Row className='d-flex' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                <ButtonGroup>
                    <MarkButton format="bold" icon=" fas fa-bold" />
                    <MarkButton format="italic" icon="fas fa-italic" />
                    <MarkButton format="underline" icon="fas fa-underline" />
                </ButtonGroup>
                <ColorButton />
                <ButtonGroup style={{ marginLeft: '4px' }}>
                    <BlockButton format="block-quote" icon="fas fa-quote-right" />
                    <BlockButton format="numbered-list" icon="fas fa-list-ol" />
                    <BlockButton format="bulleted-list" icon="fas fa-list" />
                </ButtonGroup>
            </Row>
            <Row className='d-flex mt-2' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                <ButtonGroup >
                    <BlockButton format="heading-one" icon="fas fa-heading" />
                    <BlockButton format="heading-two" icon="fas fa-h1" />
                    <BlockButton format="heading-tre" icon="fas fa-h2" />
                    <BlockButton format="heading-for" icon="fas fa-h3" />
                </ButtonGroup>
                <ButtonGroup style={{ marginLeft: '8px' }}>
                    <Button active={HAlign === 'left'} as='label' onClick={() => setHAlign('left')}>
                        <i className="fas fa-align-left"></i>
                    </Button>
                    <Button active={HAlign === 'center'} as='label' onClick={() => setHAlign('center')}>
                        <i className="fas fa-align-center"></i>
                    </Button>
                    <Button active={HAlign === 'right'} as='label' onClick={() => setHAlign('right')}>
                        <i className="fas fa-align-right"></i>
                    </Button>
                    <Button active={HAlign === 'justify'} as='label' onClick={() => setHAlign('justify')}>
                        <i className="fas fa-align-justify"></i>
                    </Button>
                </ButtonGroup>
            </Row>
            <Row className='d-flex mt-2' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                <FontsFamily />
            </Row>
            <TitleCard>FUENTE Y TEXTO</TitleCard>
        </Card.Body>
    </Card>
}
export const TBElementLite = () => {
    return <Card style={{ backgroundColor: '#eee', margin: '0 0 0 5px', width: '101px' }}>
        <Card.Body style={{ margin: '0 0 0 0', padding: '3px 3px 3px 3px' }}>
            <Row className='d-flex' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                <ButtonGroup>
                    <MarkButton format="bold" icon=" fas fa-bold" />
                    <MarkButton format="italic" icon="fas fa-italic" />
                    <MarkButton format="underline" icon="fas fa-underline" />
                </ButtonGroup>
            </Row>
            <Row className='mt-2' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                <ButtonGroup>
                    <BlockButton format="heading-two" icon="fas fa-h1" />
                    <BlockButton format="heading-tre" icon="fas fa-h2" />
                    <BlockButton format="heading-for" icon="fas fa-h3" />
                </ButtonGroup>
            </Row>
            <Row className='d-flex mt-2' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
                <FontsFamily />
            </Row>
            <TitleCard>FUENTE Y TEXTO</TitleCard>
        </Card.Body>
    </Card>
}
const getType = (n: any) => n.type;

const toggleBlock = (editor: any, format: any) => {
    const isActive = isBlockActive(editor, format)
    const isList = LIST_TYPES.includes(format)

    Transforms.unwrapNodes(editor, {
        match: n =>
            !Editor.isEditor(n) &&
            SlateElement.isElement(n) &&
            LIST_TYPES.includes(getType(n)),
        split: true,
    })
    const newProperties: any = {
        type: isActive ? 'paragraph' : isList ? 'list-item' : format,
    }
    Transforms.setNodes<SlateElement>(editor, newProperties)

    if (!isActive && isList) {
        const block = { type: format, children: [] }
        Transforms.wrapNodes(editor, block)
    }
}

const toggleMark = (editor: any, format: any) => {
    const isActive = isMarkActive(editor, format)
    if (isActive) {
        Editor.removeMark(editor, format)
    } else {
        Editor.addMark(editor, format, true)
    }
}
const getMatch = (a: any, b: any) => {
    let c: any = Editor.nodes(a, b)
    return c;
}
const isBlockActive = (editor: any, format: any) => {
    const { selection } = editor
    if (!selection) return false

    const [match] = getMatch(editor, {
        at: Editor.unhangRange(editor, selection), match: (n: any) => !Editor.isEditor(n) && SlateElement.isElement(n) && getType(n) === format,
    });

    return !!match
}

const isMarkActive = (editor: any, format: any) => {
    const marks: any = Editor.marks(editor)
    return marks ? marks[format] === true : false
}

const Element = ({ attributes, children, element }: any) => {
    switch (element.type) {
        case 'block-quote':
            return <blockquote style={{ width: '100%' }} {...attributes}>{children}</blockquote>
        case 'bulleted-list':
            return <ul style={{ width: '100%' }} {...attributes}>{children}</ul>
        case 'heading-one':
            return <h1 style={{ width: '100%' }}{...attributes}>{children}</h1>
        case 'heading-two':
            return <h2 style={{ width: '100%' }}{...attributes}>{children}</h2>
        case 'heading-tre':
            return <h2 style={{ width: '100%' }}{...attributes}>{children}</h2>
        case 'heading-for':
            return <h4 style={{ width: '100%' }}{...attributes}>{children}</h4>
        case 'list-item':
            return <li style={{ width: '100%' }}{...attributes}>{children}</li>
        case 'numbered-list':
            return <ol style={{ width: '100%' }} {...attributes}>{children}</ol>
        default:
            return <p style={{ width: '100%' }}{...attributes}>{children}</p>
    }
}

const Leaf = ({ attributes, children, leaf }: any) => {
    let style: CSSProperties = {};
    Object.keys(leaf).forEach((k: any) => {
        if (/^#[0-9A-F]{6}$/i.test(k)) style.color = k;
        if (k.indexOf('font:') > -1) {
            const s = k.replace('font:', '');
            style.fontFamily = s;
        }
    })
    if (leaf.bold) {
        children = <strong>{children}</strong>
    }

    if (leaf.code) {
        children = <code>{children}</code>
    }

    if (leaf.italic) {
        children = <em>{children}</em>
    }

    if (leaf.underline) {
        children = <u>{children}</u>
    }
    return <span style={style} {...attributes}>{children}</span>
}

export const BlockButton = ({ format, icon, style }: any) => {
    const editor = useSlate()
    return (
        <Button style={style} active={isBlockActive(editor, format)} onClick={() => toggleBlock(editor, format)}>
            <i className={icon}></i>
        </Button>
    )
}
export const MarkButton = ({ format, icon }: any) => {
    const editor = useSlate()
    return <Button active={isMarkActive(editor, format)} onClick={() => toggleMark(editor, format)}>
        <i className={icon}></i>
    </Button>

}
export const ColorButton = () => {
    const editor = useSlate();
    const [color, setColor] = useState<string>('#000000');
    useEffect(() => { toggleMark(editor, color); return () => toggleMark(editor, color) }, [color]);
    return <ButtonGroup style={{ marginLeft: '4px' }}>
        <Button as='label' onClick={() => toggleMark(editor, color)} style={{ color: color }}>
            <i className="fas fa-font"></i>
        </Button>
        <Button as='label' style={{ color: color }}>
            <i className="fas fa-palette"></i>
            <input style={{ width: 0, height: 0, position: 'absolute', top: 13, right: 15, zIndex: -3 }} value={color} type='color'
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => setColor(e.currentTarget.value)} />
        </Button>
    </ButtonGroup>
}
export const FontsFamily = () => {
    const editor = useSlate();
    const [font, setFont] = useState('font:Arial');
    const set = (e: React.ChangeEvent<HTMLSelectElement>) => setFont('font:' + e.currentTarget.value)
    useEffect(() => { toggleMark(editor, font); return () => toggleMark(editor, font) }, [font]);
    return <FormControl style={{ height: '24px', padding: '0 0 0 0' }} className='form-control-sm' as='select' onChange={set} value={font.replace('font:', '')}>
        {fontsFamily.map(c => <option style={{ fontFamily: c }} key={c}>{c}</option>)}
    </FormControl>
}
export default SlateProvider