import React, { useState } from 'react'
import { Card, Row, Button, ButtonGroup, InputGroup, FormControl } from 'react-bootstrap';
import { css } from '@emotion/css';
import { Link } from "react-router-dom"



export default function Selector() {
    return <div style={{ height: '100%', maxHeight: 'calc(100vh - 180px)' }}>
        <Link to="/panel/paralax1">
            <img width={144} height={96}src="/public/panels/pax/3.jpg" ></img>
        </Link>
        <Link to="/panel/paralax2">
            <img width={144} height={96} src="/public/panels/pax/1.jpg" ></img>
        </Link>
        <Link to="/panel/galeria">
            <img width={144} height={96} src="/public/panels/gal/1.jpg" ></img>
        </Link>
        <Link to="/panel/video">
            <img width={144} height={96} src="/public/panels/gal/5.jpg" ></img>
        </Link>
    </div>
}