import react, { useContext } from 'react';
import { getCSS, getWidth } from './props';
import { EditArea, AlingContext } from '../../SlateCustom'
import { Row, Col } from 'react-bootstrap';
import { State } from './index'

export function Panel({ config: { image, opacity, color, xs, sm, md, lg, xl, xxl, size } }: { config: State }) {
    const { r, g, b } = ToRGB(color);
    const { HAlign } = useContext(AlingContext);
    return <div className={getCSS(image)}>
        <div className="container" style={{ height: '100%' }}>
            <Row className={getWidth(xs, sm, md, lg, xl, xxl, size)}>
                <Col style={{ backgroundColor: `rgba(${r},${g},${b},${opacity})`, height: '100%' }}>
                    <div className={`text-${HAlign}`} style={{ height: '100%', width: '100%' }}><EditArea /></div>
                </Col>
            </Row>
        </div>
    </div>
}

function ToRGB(hex: string) {
    if (hex.charAt(0) == "#") hex = hex.substring(1, hex.length);
    let red = hex.substring(0, 2);
    let green = hex.substring(2, 4);
    let blue = hex.substring(4, 6);
    let r = parseInt(red, 16);
    let g = parseInt(green, 16);
    let b = parseInt(blue, 16);
    return { r, g, b };
}